import { FadeIn } from './AnimateIn'
import { Text } from './Text'

interface Props {
  title: string
  index: number
  children: string
}

export default function Leader({ title, index, children }: Props) {
  return (
    <FadeIn delay={index * 0.1 + 0.2} className='w-full md:w-[calc(50%-8px)] lg:w-full'>
      <div className='flex flex-col p-[24px] gap-[8px] bg-white rounded-[8px]'>
        <div className='uppercase text-neutral-400 text-[14px] font-medium leading-[1.8] '>
          {title}
        </div>
        <Text.Body>Bro. {children}</Text.Body>
      </div>
    </FadeIn>
  )
}
